* {
  box-sizing: border-box;
}

body {
  color: #212121;
  background-color: #9fdad7;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

input {
  border: 1px solid #2b9fe298;
  border-radius: 8px;
  outline: none;
  margin-right: 8px;
  padding: 5px;
}

input:focus {
  border-color: #188ce8;
}

button {
  cursor: pointer;
  background-color: #2b9fe298;
  border: 1px solid #2b9fe298;
  border-radius: 8px;
  align-items: center;
  padding: 5px 10px;
  font-weight: 600;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

